
.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
      visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.is-hidden{
        opacity: 0;
        pointer-events: none;
        overflow: hidden;
}

.is-open {
  opacity: 1;
  pointer-events: auto;
  overflow: visible
}

// .modal {
