//   }
.modal {
  display: inline-block;
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 50%;
  // margin-top: 20px;
  min-width: 280px;
  max-height: 100vh;
  background-color: #ffffff;
  
  overflow-y: auto;
  transform: translate(-50%, -50%);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  @media screen and (min-width: 768px){
      width: 704px;
     
      display: flex;
      height: 552px;
    }
  
    @media screen and (min-width: 1024px) {
      
      width: 806px;
      height: 558px;
    }

}



.modal-conteiner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  padding: 48px 20px;
  margin: 0;
  background-color: #ffffff;
  border-radius: 4px;
  box-shadow: 0 0 20px rgba(0,0,0,.2);
  overflow: hidden;
  @media screen and (min-width: 768px){

      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      padding: 40px 36px;
      margin: 0;
      background-color: #ffffff;
      border-radius: 4px;
      box-shadow: 0 0 20px rgba(0,0,0,.2);
      overflow: hidden;
    }
    @media screen and (min-width: 1024px) {
      padding: 40px 12px;
    }
    
}

