  .modal-conteiner-img {
    width: 240px;
    height: 357px;
    border-radius: 5px;
    height: 356px;
    object-fit: contain;
  
    @media (min-width: 768px) {
      width: 264px;
      height: 373px;
    }
  
    @media (min-width: 1024px) {
      width: 375px;
      height: 478px;
    }
  }
  
  .card-title {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.15;
    padding-top: 30px;
    padding-bottom: 20px;
  
    color: #000000;
  
    @media (min-width: 767px) {
      padding-top: 0;
    }
    @media (min-width: 1024px) {
      font-size: 30px;
      line-height: 1.16;
    }
  }
  .card-list {
    @media (min-width: 767px) {
      display: flex;
      flex-direction: column;
    }
  }
  
  .card-item {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;
  
    color: #8c8c8c;
  
    padding-bottom: 10px;
    &:nth-child(4n) {
      padding-bottom: 20px;
    }
  
    float: left;
    width: 100%;
    @media (min-width: 1024px) {
      width: 49%;
    }
  }
  
  .card-item-average {
    padding: 1px 10px;
    background: #ff6b01;
    border-radius: 5px;
    color: #ffffff;
    margin-right: 3px;
  }
  
  .card-item-vote,
  .card-item-popularity,
  .card-item-original-title,
  .card-item-genres {
    float: right;
    width: 53%;
  
    @media (min-width: 1024px) {
      width: 49%;
    }
  }
  
  .card-item-count,
  .card-item-popularity,
  .card-item-original-title {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.16;
    text-transform: uppercase;
  
    color: #000000;
    @media (min-width: 768px) {
      font-size: 12px;
      line-height: 1.33;
    }
  }
  
  .card-item-genre {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.16;
  
    color: #000000;
    @media (min-width: 768px) {
      font-size: 12px;
      line-height: 1.33;
    }
  }
  
  .card-description {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;
  
    padding-bottom: 10px;
    text-transform: uppercase;
  
    color: #000000;
  }
  
  .card-text {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.67;
    padding-bottom: 20px;
    color: #000000;
  }
  
  .card-item-count {
    padding: 1px 5px;
    background: #f7f7f7;
    border-radius: 5px;
  }
  
  .card-list-btn {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
  
    &:nth-child(2n) {
      margin: 0;
    }
  
    @media (min-width: 768px) {
      &:nth-child(2n) {
        margin-top: 10px;
      }
    }
  
    @media (min-width: 1024px) {
      &:nth-child(2n) {
        margin-top: 0;
      }
    }
  }
