  .card-btn-watched,
  .card-btn-que {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;
    display: flex;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    padding: 14px 10px;
    border: 1px solid #000000;
    border-radius: 5px;
    color: #000000;
    cursor: pointer;
    // margin-bottom: 10px;
    margin-right: 15px;
    margin-bottom: 0;
    width: 110px;
    height: 44px;
    @media (min-width: 768px) {
      padding: 14px 10px;
      width: 125px;
      height: 44px;
     
    }
    @media (min-width: 1024px) {
      padding: 12px 10px;
      width: 136px;
      height: 44px;
    }
  }
  
  
  .card-btn-watched:hover,
  .card-btn-que:hover {
    color: #ffffff;
    background-color: #ff6b01;
    border: none;
    outline: none;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1) background-color 250ms
      cubic-bezier(0.4, 0, 0.2, 1);
      margin-right: 15px;
      margin-bottom: 0;
     
  }
  
  .modal-close-btn {
    position:absolute;
    z-index: 10;
    background-color: transparent;
    display:block;
    top: 10px;
    right: 10px;
    width: 30px;
    height: 30px;
    fill: #000000;
    border:none;
    outline: none;
  }

  .button-vector {
    fill: #000000;
    transition-property: fill;
     
  }
  
  .button-vector:hover,
  .button-vector:focus {
    fill: #ff6b01;
  }

  
  .modal {
    position: relative;
  }
  























