.footer {
    background-color: #F7F7F7;
    padding-top: 28px;
    padding-bottom: 28px;

    @media screen and (min-width: 768px) {
        padding-top: 30px;
        padding-bottom: 30px;
    }

    @media screen and (min-width: 1280px) {
        padding-bottom: 28px;
    }
}

.footer-div {
    @media screen and (min-width: 768px) {
        display: flex;
        justify-content: center;

    }
}

.footer-text {
    font-family: 'Roboto';
    font-weight: 400;
    font-size: 14px;
    line-height: calc(16/14);
    color: #545454;
    text-align: center;

    @media screen and (min-width: 768px) {
        font-size: 16px;
        line-height: calc(16/18);
    }

}

.footer-link {
    font-family: 'Roboto';
    font-weight: 400;
    font-size: 14px;
    line-height: calc(16/41);
    color: #545454;

    @media screen and (min-width: 768px) {
        font-size: 16px;
        line-height: calc(16/18);
    }
}